class Tool {

  async fetchAPI(uri){
    try{
      let res = await fetch(uri);
      return await res.json();
    }catch(error){
      console.log(error);
    }
  }

  async fetchPostAPI(uri, data){
    const option = {
      method: "POST", 
      body: JSON.stringify(data),
      headers: {'Content-type': 'application/json; charset=UTF-8'}
    }

    try{
      let res = await fetch(uri, option);
      return await res.json();
    }catch(error){
      console.log(error);
    }
  }
  
}// class end 

const tool = new Tool();

export default tool;